.header {
  display: flex;
  height: 50px;
  align-items: center;
  justify-content: center;
  background-color: #aaaaaa;
}

.stage {
  position: absolute;
  top: 50px;
  left: 0;
}

.image {
  opacity: 0.2;
}

.zoomContainer {
  position: fixed;
  top: 30px;
  right: 30px;
  width: 200px;
  height: 200px;
}

.zoomInner {
  position: relative;
  width: 100%;
  height: 100%;
}

.mask {
  top: 0;
  right: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.zoom {
  position: absolute;
  margin-left: 100px;
  margin-top: 100px;
  transform-origin: top left;
  transform: scale(2);
}

.target {
  width: 20px;
  height: 20px;
  border-radius: 13px;
  z-index: 1000;
  border: 3px solid red;
}
